import { Injectable } from '@angular/core';
import { Http } from '@angular/http';
import { getCookie, setCookie, getCookieObject } from './Cookies';
import { myWebsiteDomain } from './config';
@Injectable({
  providedIn: 'root'
})
export class AdminManagerService {

  constructor(private http: Http) {
  }
  getComputers() {
    const url = myWebsiteDomain + '/admin/listComputers';
    return this.http.post(url, getCookieObject(), { withCredentials: true })
      .toPromise()
      .then(res => {
        return res.json();
      }).catch(e => false);
  }

  getOverView(data) {
    const body = getCookieObject();
      body.data = data;
    if (data.mode == 'computer') {
      const url = myWebsiteDomain + '/admin/overviewByUser';
      return this.http.post(url, body, { withCredentials: true })
        .toPromise()
        .then(res => {
          return res.json();
        }).catch(e => false);
    }
    if (data.mode == 'website') {
      const url = myWebsiteDomain + '/admin/overviewByWebsite';
      return this.http.post(url, body, { withCredentials: true })
        .toPromise()
        .then(res => {
          return res.json();
        }).catch(e => false);
    }
  }

  getListWeb() {
    const url = myWebsiteDomain + '/admin/listWebsite';
      return this.http.post(url, getCookieObject(), { withCredentials: true })
        .toPromise()
        .then(res => {
          return res.json();
        }).catch(e => false);
  }

  updateListWeb(listweb) {
    const body = getCookieObject();
      body.data = listweb;
    const url = myWebsiteDomain + '/admin/listWebsite';
      return this.http.put(url, body, { withCredentials: true })
        .toPromise()
        .then(res => {
          return res.json();
        }).catch(e => false);
  }

  getComputerInfo(computerID, firstID) {
    const url = myWebsiteDomain + '/admin/computerInfo/' + computerID + '/' + firstID + '/20';
      return this.http.post(url, getCookieObject(), { withCredentials: true })
        .toPromise()
        .then(res => {
          return res.json();
        }).catch(e => {console.log(e); return false; });
  }
}
